/* CookiesInformation.css */
.cookies-information {
    z-index: 1000;
    position: fixed;
    bottom: 0;
    right: 0;
    width: 500px;
    height: 220px;
    padding: 10px;
    background-color: #222;
    color: #fff;
    display: flex;
    /* justify-content: space-between; */
    align-content: space-between;
    font-family: 'Space Mono', monospace;
    font-size: x-small;
  }
  
  .cookies-header {
    display: flex;
    align-items: center;
  }
  
  .cookies-header h3 {
    margin: 0;
    margin-right: 20px;
  }
  
  .cookies-content {
    flex: 1;
    padding-right: 20px;
  }
  
  .cookies-buttons button {
    margin-left: 10px;
  }
  
  .cookies-buttons button:first-child {
    margin-left: 0;
  }
  