.img-container {
    position: absolute;
    display: block;
    opacity: 35%;
    /* background-color: blue; */
    margin-top: 100px;
}

img {
    display: block;
    -webkit-transition: opacity cubic-bezier(0, 0.52, 1, 1) 2s;
       -moz-transition: opacity cubic-bezier(0, 0.52, 1, 1) 2s;
        -ms-transition: opacity cubic-bezier(0, 0.52, 1, 1) 2s;
         -o-transition: opacity cubic-bezier(0, 0.52, 1, 1) 2s;
            transition: opacity cubic-bezier(0, 0.52, 1, 1) 2s;
    
}

div > .hover{
    opacity: 0;
    z-index: 1;
}

div:hover > .hover{ 
    opacity: 1;
    z-index: 2;
    transition-duration: 500ms;
}